.row-trending {
    padding-top: 30px;
}
.image-blogheader {
    text-align: center;
}
.img-logo {
    width: 100%;
    margin: 0 auto;
}
.image-blogheader {
  display: block;
  border-radius: 4px;
  transition: border .2s ease-in-out;
  overflow: hidden;
  border: 4px solid #e7e7e7;
}
.image-blogheader img {
    transition-duration: 0.5s;
}
.image-blogheader img:hover {
  transform: scale(1.2);
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -o-transform: scale(1.2);
  -ms-transform: scale(1.2);
  cursor: pointer;
}
