.type-feedback {
    font-size: 30px;
    text-align: center;
    padding-top: 130px;
    color: rgba(255, 0, 0, 0.8);
}
.backgroud-feedback {
    background: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), url("img/feedback1.jpg") center center;
    background-size: cover;
    height: 500px;
    padding: 80px 0 60px 0;
    position: relative;
}
.box {
    position: absolute;
    top: 85%;
    left: 52%;
    width: 10px;
    height: 10px;
    border: 1px solid #d45a5a;
    transform-origin: top left;
    transform: scale(0) rotate(0deg) translate(-50%, -50%);
    animation: animate 6s ease-in forwards infinite;
}
.box:nth-child(2)
{
    left: 12%;
    top: 14%;
    animation-delay: 1s;
}
.box:nth-child(3)
{
    left: 85%;
    top: 65%;
    animation-delay: 2s;
    border-color: #000;
}
.box:nth-child(4)
{
    left: 73%;
    top: 10%;
    animation-delay: 3s;
}
.box:nth-child(5)
{
    left: 10%;
    top: 85%;
    animation-delay: 4s;
    border-color: #000;
}
.box:nth-child(6)
{
    left: 50%;
    top: 10%;
    animation-delay: 5s;
}
@keyframes animate {
    from {
        transform: scale(0) rotate(0deg) translate(-50%, -50%);
        opacity: 1;
    }
    to {
        transform: scale(20) rotate(720deg) translate(-50%, -50%);
        opacity: 0;
    }
}