.body-post {
  padding:20px;
  background:#fff;
  display:block;
}
.body-post details {
    display:inline;
}
.img-post-body {
    margin: 0 auto;
}
.body-post h3 {
    color: #000;
    font-size: 18px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    line-height: 40px;
    padding-left: 15px;
    margin-bottom: 0px;
    background: #f4f4f4;
    border-left: 3px solid #03c4eb;
    margin-bottom: 10px;
}
details {
    position:relative;
  }
  details summary {
    display:block;
    cursor: pointer;
    color: #03c4eb;
  }
  details summary:focus {
    outline:none;
  }
  details[open] {
    display:block;
    padding-bottom:25px;
    padding-top:10px;
    animation: open .2s linear;
  }
  details[open] summary {
    position:absolute;
    bottom: 0;
    left:0;
  }
  
  details #open{padding-left:5px;text-align:middle;}
  details #open:after{
      display: inline-block;
      position:relative;
      top: -3px;
      padding-left: 8px;
      content: "\00bb";
      transform: rotate(90deg);
  }
  details[open] #open{display:none;}
  details #close{display:none;}
  details[open] #close{display:block;}
  
  ::-webkit-details-marker {display: none;}
  @keyframes open {
    0% {
      opacity: 0;
      transform: translateY(-10px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  .comment-box .below-comments {
    color: #000;
    font-size: 18px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    line-height: 40px;
    padding-left: 15px;
    margin-bottom: 0px;
    background: #f4f4f4;
    border-left: 3px solid #03c4eb;
    margin-bottom: 10px;
  }
  .input-comments {
      outline: none;
      width: 50%;
      margin-bottom: 10px;
      height: 30px;
  }
  .text-area-comments {
      outline: none;
      width: 100%;
  }
  .btn-postComments {
      outline: none;
      background: #c3011a;
      color: #fff;
      border: none;
      float: left;
      padding: 10px;
      margin-right: 10px;
  }
  .btn-postComments:hover {
    background: #d1010e;
}
#comment-reveal {
    outline: none;
      background: #c3011a;
      color: #fff;
      border: none;
      padding: 10px;
}
#comment-reveal:hover {
      background: #d1010e;
}
.img-user-comment {
    width: 80px;
    float: left;
    margin-right: 15px;
    border-radius: 50%;
}
.comment-body {
    color: #797979;
}
.comments-bg {
    background: #f4f4f4;
    width: 72%;
    margin-bottom: 10px;
}

.wrapper_star {
  width: auto;
  margin-top: 15px;
}

.star {
  font-size: 25px;
  margin-right: 5px;
}

.star.active {
  color: #ff9727;
}

.star_message {
  font-size: 16px;
  margin-top: 15px;
}