
.top10-phone {
    background: #03c4eb;
    color: #fff;
    position: relative;
    width: 40%;
    padding: 10px;
    &::before {
        content: '';
        position: absolute;
        border-left: 50px solid #03c4eb;
        border-top: 23px solid transparent;
        border-bottom: 23px solid transparent;
        left: 100%;
        top: 0;
    }
    margin-bottom: 0;
}
.banner-samsung {
    border: 2px dashed #dbdbdb;
}
.samsung-top10 {
    border-right: 1px solid #dbdbdb;
    padding: 10px 20px;
    position: relative;
    .sale-samsung {
        position: absolute;
        top: 10px;
        left: 25px;
        border-radius: 50%;
        background: red;
        padding: 7px;
        color: #fff;
        border: 1px dashed red;
    }
    &:hover img {
        margin-top: -5px;
    }
}
.samsung-galaxy {
    .swiper-container {
        position: relative;
        .swiper-button-next {
            position: absolute;
            right: 0;
        }
        .swiper-button-prev {
            position: absolute;
            left: 0;
        }
    }
}