.post-samsung {
    padding-bottom: 10px;
    border-bottom: 1px solid #e7e7e7;
}
.title-post-samsung a{
    text-decoration: none;
    color: #000;
}
.title-post-samsung a:hover{
    color: #03c4eb;
}
.span-start i:nth-child(1) {
    color: #ff9727;
}
.span-start i:nth-child(2) {
    color: #ff9727;
}
.span-start i:nth-child(3) {
    color: #ff9727;
}
.span-start i:nth-child(4) {
    color: #ff9727;
}
.span-start i:nth-child(5) {
    color: #f3f3f3;
}
.span-start {
    padding-right: 10px;
}
.old-samsung {
    background: #f9f9ff;
    padding: 10px 35px;
    border: 1px solid #c6c3d4;
}
.color-old-samsung {
    color: #03c4eb;
}
.icon-gift i{
    color: #ff9727;
}
.icon-gift .fa-gift{
    font-size: 25px;
    padding-right: 10px;
}
.icon-gift .fa-car{
    font-size: 20px;
    padding-right: 10px;
}
.icon-gift .fa-shield{
    font-size: 30px;
    padding-right: 10px;
}
.icon-gift .fa-money{
    font-size: 20px;
    padding-right: 10px;
}
.slide-samsung {
    padding-top: 20px;
}
.col-samsung-padding {
    padding-top: 20px;
}
.buy-now-samsung {
    background: #f9f9ff;
    border: 1px solid #c6c3d4;
    text-align: center;
    font-size: 20px;
}
.special-samsung {
    background: #34a105;
    color: #fff;
    font-size: 16px;
    padding: 10px;
    margin-top: 20px;
    border: 1px dashed #c6c3d4;
}
.description-samsung {
    border: 1px dashed #c6c3d4;
    background: #f9f9ff;
}
.text-description {
    padding-left: 15px;
    position: relative;
}
.text-description::after {
    position: absolute;
    content: '';
    width: 10px;
    height: 10px;
    background: #34a105;
    top: 5px;
    left: 0;
    border-radius: 50%;
}
.slide-samsung .swiper-container .swiper-button-next {
    opacity: 0 !important;
}
.slide-samsung .swiper-container .swiper-button-prev {
    opacity: 0 !important;
}
.slide-samsung .swiper-container:hover .swiper-button-next {
    opacity: 1 !important;
}
.slide-samsung .swiper-container:hover .swiper-button-prev {
    opacity: 1 !important;
}
.buynow-samsung {
    margin-top: 20px;
    width: 100%;
    background: #c3011a;
    outline: none;
    border: none;
    color: #fff;
    padding: 10px;
}
.buynow-ss {
    font-size: 16px;
}
.btn-left-buynow {
    margin-top: 10px;
    float: left;
    width: 48%;
    background: #34a105;
    outline: none;
    border: none;
    color: #fff;
    padding: 10px;
}
.btn-right-buynow {
    margin-top: 10px;
    float: right;
    width: 48%;
    background: #34a105;
    outline: none;
    border: none;
    color: #fff;
    padding: 10px;
}