
.row-post-product {
    padding-top: 10px;
    margin-top: 30px;
    border-top: 1px solid #e7e7e7;
    margin-bottom: 30px;
}
.img-update {
    margin: 0 auto;
    width: 100%;
    padding-left: 30px;
    padding-right: 30px;
}
.title-update {
    color: #000;
}
.title-update:hover {
    color: #03c4eb;
}
.center-text {
    text-align: center;
}
.title_post_product {
    background: #f9f9ff;
    border-left: 3px solid #03c4eb;
    margin-bottom: 10px;
  }
  
  .title_post_product h2 {
    color: #000;
    font-size: 18px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    line-height: 40px;
    padding-left: 15px;
    margin-bottom: 0px;
  }
.row-post-product .swiper-container .swiper-button-next {
    opacity: 0 !important;
}
.row-post-product .swiper-container .swiper-button-prev {
    opacity: 0 !important;
}
.row-post-product .swiper-container:hover .swiper-button-next {
    opacity: 1 !important;
}
.row-post-product .swiper-container:hover .swiper-button-prev {
    opacity: 1 !important;
}