.tab-allproducts {
    padding-top: 30px;
}
.product_item {
    &:hover .allproduct_pic .allproduct_pic_hover {
        bottom: 20px;
    }
    &:hover .allproduct_text h4 a {
        color: #03c4eb;
    }
    border: 1px solid #adb0b5;
    margin-bottom: 20px;
    padding: 10px 0;
}
.allproduct_pic {
	height: 270px;
	position: relative;
	overflow: hidden;
	background-position: center center;
}
.allproduct_pic_hover {
	position: absolute;
	left: 0;
	bottom: -50px;
	width: 100%;
	text-align: center;
    transition: all, 0.5s;
    li {
        list-style: none;
        display: inline-block;
        margin-right: 6px;
        a {
            font-size: 16px;
            color: #000;
            height: 40px;
            width: 40px;
            line-height: 40px;
            text-align: center;
            border: 1px solid #f4f4f4;
            background: #ffffff;
            display: block;
            border-radius: 50%;
            transition: all, 0.5s;
            i {
                position: relative;
                transform: rotate(0);
                transition: all, 0.3s;
            }
        }
        &:hover a {
            background: #03c4eb;
            border-color: #03c4eb;
        }
        &:hover a i {
            color: #ffffff;
            transform: rotate(360deg);
        }
    }
}
.allproduct_text {
	text-align: center;
    padding-top: 15px;
    h4 {
        a{
            text-decoration: none;
            color: #000;
        }
    }
    h5 {
        color: #adb0b5;
        font-weight: bold;
    }
}