.premium-about{
  text-align: center;
  color: #03C4EB;
  position: relative;
}
.premium-about:after {
  position: absolute;
  content: '';
  width: 35%;
  top: calc(50% + 2px);
  margin-left: 20px;
  border: 1px solid #03C4EB;
}
.premium-about:before {
  position: absolute;
  content: '';
  width: 35%;
  left: 5%;
  top: calc(50% + 2px);
  margin-left: 20px;
  border: 1px solid #03C4EB;
}
.title-about {
  text-align: center;
  padding-bottom: 20px;
}
  .premium{
    max-width: 1000px;
    margin: auto;
    padding: 40px;
    display: flex;
    flex-wrap: wrap;
  }
  
  .pricing-table{
    background: #f1f1f1;
    flex: 1;
    margin: 10px;
    text-transform: uppercase;
  } 
  .pricing-table a {
      text-decoration: none;
  }
  .pricing-table > ul > li {
    list-style: none;
}
  .pricing-header{
    color: #fff;
    position: relative;
    margin-bottom: 20px;
  }
  
  .price{
    font-size: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px 0;
  }
  
  .price span{
    font-size: 30px;
  }
  
  .title{
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%,50%);
    width: 80%;
    max-width: 360px;
    text-align: center;
    padding: 8px 0;
    font-size: 14px;
  }
  
  .pricing-list{
    padding: 10px 20px;
  }
  
  .pricing-list li{
    margin: 8px 0;
    font-size: 14px;
    color: #344;
    text-align: center;
  }
  
  .border{
    width: 80px;
    height: 1px;
    background: #ddd;
    margin: auto;
  }
  
  .pricing-table a{
    display: block;
    width: 80%;
    margin: auto;
    margin-bottom: 20px;
    text-align: center;
    padding: 8px 0;
    font-size: 14px;
    border: 1px solid;
    max-width: 360px;
    transition: .2s linear;
  }
  .pricing-table a:hover{
    border-radius: 30px;
  }
  .table1 .pricing-header{
    background: #2980b9;
  }
  .table2 .pricing-header{
    background: #2c3e50;
  }
  .table3 .pricing-header{
    background: #c0392b;
  }
  
  .table1 .title{
    background-image: linear-gradient(125deg,#3498db,#9b59b6);
  }
  .table2 .title{
    background-image: linear-gradient(125deg,#34495e,#95a5a6);
  }
  .table3 .title{
    background-image: linear-gradient(125deg,#e74c3c,#f39c12);
  }
  .table1:hover {
    background-image: linear-gradient(125deg,#3498db,#9b59b6);
    margin: -10px 0;
  }
  .table2:hover {
    background-image: linear-gradient(125deg,#34495e,#95a5a6);
    margin: -10px 0;
  }
  .table3:hover {
    background-image: linear-gradient(125deg,#e74c3c,#f39c12);
    margin: -10px 0;
  }
  .table1 a{
    color: #2980b9;
  }
  .table2 a{
    color: #2c3e50;
  }
  .table3 a{
    color: #c0392b;
  }
  
  .table1 a:hover{
    background: #2980b9;
    color: #fff;
  }
  .table2 a:hover{
    background: #2c3e50;
    color: #fff;
  }
  .table3 a:hover{
    background: #c0392b;
    color: #fff;
  }
  
  @media screen and (max-width:768px) {
    .pricing-table{
      flex: 100%;
    }
    .table1:hover {
      background-image: linear-gradient(125deg,#3498db,#9b59b6);
      margin: 0;
    }
    .table2:hover {
      background-image: linear-gradient(125deg,#34495e,#95a5a6);
      margin: 0;
    }
    .table3:hover {
      background-image: linear-gradient(125deg,#e74c3c,#f39c12);
      margin: 0;
    }
    .premium-about {
      &::before, &::after {
        display: none;
      }
    }
  }
  @media screen and (min-width:769px) and (max-width:992px){
    .premium-about::before {
      position: absolute;
      content: '';
      width: 30%;
      top: calc(50% + 2px);
      margin-left: 20px;
      border: 1px solid #03C4EB;
    }
  }