.hero {
    height: 100vh;
    width: 100%;
    background-image: url(../img/sky.jpg);
    background-size: cover;
    background-position: center;
    position: relative;
    overflow-x: hidden;
}
.highway {
    height: 200px;
    width: 500%;
    display: block;
    background-image: url(../img/road.jpg);
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    background-repeat: repeat-x;
    animation: highway 5s linear infinite;
}
.city {
    height: 250px;
    width: 500%;
    background-image: url(../img/city.png);
    position: absolute;
    bottom: 200px;
    left: 0;
    right: 0;
    display: block;
    z-index: 1;
    background-repeat: repeat-x;
    animation: city 20s linear infinite;
}
@keyframes highway {
    100% {
        transform: translateX(-3000px);
    }
}
@keyframes city {
    100% {
        transform: translateX(-1400px);
    }
}
.car {
    width: 400px;
    left: 50%;
    bottom: 100px;
    transform: translateX(-50%);
    position: absolute;
    z-index: 2;
}
.car img {
    width: 100%;
}
.wheel {
    left: 50%;
    bottom: 178px;
    transform: translateX(-50%);
    position: absolute;
    z-index: 2;
}
.wheel img {
    width: 72px;
    height: 72px;
    animation: wheel 1s linear infinite;
}
.back-wheel {
    left: -165px;
    position: absolute;
}
.front-wheel {
    left: 80px;
    position: absolute;
}
@keyframes wheel{
    100% {
        transform: rotate(360deg);
    }
}

  .text-hero {
   position: relative;
  }
  .text-hero h2 {
    padding-top: 30px;
    position: absolute;
    color: #fff;
    left: 30%;
    font-size: 80px;
  }
  .text-hero h2:nth-child(1) {
    color: transparent;
    -webkit-text-stroke: 2px rgba(255, 0, 0, 0.8);
  }
  .text-hero h2:nth-child(2) {
    color: rgba(255, 0, 0, 0.8);
    animation: animatess 4s ease-in-out infinite;
  }
  @keyframes animatess {
      0%, 100% {
          clip-path: polygon(0% 45%, 15% 44%, 32% 50%, 54% 60%, 70% 61%, 
          84% 59%, 100% 52%, 100% 100%, 0% 100%);
      }
      50% {
        clip-path: polygon(0% 60%, 16% 65%, 34% 66%, 51% 62%, 67% 50%,
         84% 45%, 100% 46%, 100% 100%, 0% 100%);
    }
  }
  @media screen and (max-width: 768px) {
    @keyframes highway {
        100% {
            transform: translateX(-1000px);
        }
    }
  }