.row-news {
    padding-bottom: 30px;
}
.gad_btn {
    background: #03c4eb;
    color: #fff;
    display: inline-block;
    padding: 0px 20px;
    line-height: 30px;
    border-radius: 15px;
    font-size: 12px;
    font-weight: 500;
    font-family: "Roboto", sans-serif;
}
.main_title2 {
    background: #f9f9ff;
    border-left: 3px solid #03c4eb;
    margin-bottom: 30px;
  }
  
  .main_title2 h2 {
    color: #000;
    font-size: 18px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    line-height: 40px;
    padding-left: 15px;
    margin-bottom: 0px;
  }
  .choice_text {
    margin-top: 20px;
  }
  .choice_text a{
    text-decoration: none;
  }
  
  .choice_text h4 {
    font-size: 18px;
    line-height: 27px;
    color: #000;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    margin-bottom: 15px;
    -webkit-transition: all 300ms linear 0s;
    -o-transition: all 300ms linear 0s;
    transition: all 300ms linear 0s;
  }
  
  .choice_text h4:hover {
    color: #03c4eb;
  }
  
  .choice_text p {
    margin-bottom: 0px;
  }
  .date {
    margin-bottom: 15px;
  }
  
  .date .gad_btn {
    color: #fff;
  }
  
  .date a {
    color: #777777;
    font-size: 12px;
    font-family: "Open Sans", sans-serif;
    margin-right: 15px;
  }
  
  .date a i {
    padding-right: 8px;
  }
  
  .date a:last-child {
    margin-right: 0px;
  }
  .react-tabs__tab-list {
      list-style: none;
  }
.padding-tabliss {
    padding-top: 20px;
}
.img-popular {
  width: 100px;
}
.free-game {
  background: #07a358;
  color: #fff;
  padding: 10px 5px;
  margin-left: -30px;
  text-align: center;
}
.title-popular a{
  font-size: 18px;
  margin-left: -30px;
  text-decoration: none;
  color: #000;
}
.title-popular a:hover{
  color: #03c4eb;
}
.post.post-row .post-body-tab {
  margin-left: calc(40% + 5px);
  margin-top: -25px;
}
@media screen and(min-width:993px) and(max-width:1200px) {
  .post.post-row .post-body-tab {
    margin-left: calc(40% + 25px);
    margin-top: -25px;
  }
}
@media screen and(max-width:992px) {
  .post.post-row .post-body-tab {
    margin-left: calc(20% + 25px);
    margin-top: -25px;
  }
  .free-game {
    width: 60%;
    text-align: none;
  }
}