.play-icon {
    margin-top: 200px;
    width: 55px;
    height: 55px;
    position: fixed;
    right: 35px;
    bottom: 35px;
    z-index: 2;
    border-radius: 50%;
    border: 1px solid #fff;
    background: #fff;
    font-size: 35px;
    animation: key-animation 1s linear infinite;
}
.play-icon-chat {
    position: absolute;
    top: calc(15% + 1px);
    left: calc(15% + 1px);
    color: rgba(255, 0, 0, 0.8);
}

@keyframes key-animation {
    0% {
        box-shadow: 0 0 0 0 rgba(255, 0, 0, 0.4), 0 0 0 10px rgba(255, 0, 0, 0.4), 0 0 0 20px rgba(255, 0, 0, 0.4);
    }
    100% {
        box-shadow: 0 0 0 10px rgba(255, 0, 0, 0.4), 0 0 0 20px rgba(255, 0, 0, 0.3), 0 0 0 30px rgba(255, 0, 0, 0.2);
    }
}
