.modal {
  display: none;
}

.modal.modal--is-open {
  display: block;
}
.modal-message {
  background: #fff;
  position: fixed;
  bottom: 0;
  z-index: 4;
  height: 450px;
  right: 0;
}
.btn-closeMessage {
  padding: 10px;
  position: absolute;
  top:0;
  outline: none;
  border: none;
  background: #ff9727;
  color: #000;
  right: 0;
  margin-top: 5px;
}
.img-admin-chat img{
  border-radius: 50%;
  width: 50px;
  height: 50px;
}
.img-admin-chat{
  position: relative;
}
.img-admin-chat::before {
  position: absolute;
  content: ' ';
  width: 10px;
  top: 0;
  left: 10%;
  background: green;
  border-radius: 50%;
}
.popup-chat {
  height: 100%;
  width: 400px;
}
.bg-popup-chat {
  background: #ff9727;
}
.online-message {
  position: relative;
  &::before {
    width: 15px;
    background: #34a105;
    border-radius: 50%;
    height: 15px;
    top: -12px;
    right: -45px;
    position: absolute;
    content: '';
  }
}
.contact-admin {
  position: relative;
  &::before {
    top: -35px;
    left: 60px;
    position: absolute;
    content: 'Admin';
  }
}


* {
  box-sizing: border-box;
}

.app {
  width: 400px;
  height: 400px;
  display: flex;
  flex-direction: row;
  .contact-list {
    background-color: #fff;
    width: 200px;
    overflow: auto;
    .title {

      padding-bottom: 0;
      font-size: 150%;
    }
    
    ul {
      list-style: none;

      
      li {

        position: relative;
        cursor: pointer;
        transition: all .2s ease-out;
        
        &.active, &:hover {
          background-color: lighten(#3498db, 40%);
          color: #3498db;
          border-radius: 6px;
        }
      }
    }
  }
  
  .messages {
    background-color: #fff;
    border-radius: 6px;
    overflow: hidden;
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    .messages-history {
      overflow: auto;
      flex: 1 1 auto;
      display: flex;
      flex-direction: column-reverse;
      align-items: end;
      .message {
        width: 100%;
        padding: 12px;
        padding-top: 0;
        &:last-child {
          padding-top: 12px;
        }
        
        &.me {
          text-align: right;
          .message-body {
            color: #fff;
            padding: 5px;
            background-color: #3498db;
          }
        }
        
        .message-body {
          background-color: #DDD;
          color: #333;
          font-weight: 400;
          display: inline-block;
          padding: 12px / 1.5 12px;
          border-radius: 6px;
          max-width: 250px;
          min-width: 75px;
          line-height: 20px;
          padding: 5px;
          text-align: initial;
        }
      }
    }
    
    .messages-inputs {
      display: flex;
      input[type="text"] {
        background-color: transparent;
        border: none;
        padding: 12px;
        resize: vertical;
        flex: 1 1 auto;
        outline: none;
      }
      .btn-sendMessage {
        padding: 0 12px;
        background-color: transparent;
        border: none;
        outline: none;
        cursor: pointer;
        border-radius: none;
        &:hover {
          background: #ff9727;
        }
      }
    }
  }
}