.service-about2{
  text-align: center;
  color: #03C4EB;
  position: relative;
}
.service-about2:after {
  position: absolute;
  content: '';
  width: 40%;
  top: calc(50% + 2px);
  margin-left: 20px;
  border: 1px solid #03C4EB;
}
.service-about2:before {
  position: absolute;
  content: '';
  width: 40%;
  left: 0;
  top: calc(50% + 2px);
  margin-left: 20px;
  border: 1px solid #03C4EB;
}
.title-p2 {
  text-align: center;
  padding-bottom: 20px;
  font-size: 14px !important;
}
.jumbotron {
    margin-top: 20px;
  }
  .jumbotron .p-3 {
    padding: 1.2rem !important;
  }
  .jumbotron .card {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 2px solid rgba(0,0,0,0.125);
    border-radius: .25rem;
  }
  .jumbotron .card .my-card {
    position: absolute;
    left: 40%;
    top: -20px;
    border-radius: 50%;
  }
  .jumbotron .card .shadow {
    box-shadow: 0 .5rem 2rem rgba(0,0,0,0.15) !important;
  }
  .jumbotron .card .text-center {
    text-align: center !important;
  }
  .jumbotron .card .text-center .mt-h4 {
    font-size: 25px;
  }
  .jumbotron .card .text-center .counter {
    font-size: 28px;
  }
  .jumbotron .svg-inline--fa.fa-w-16 {
    font-size: 20px;
  }
  .jumbotron .svg-inline--fa.fa-w-18 {
    font-size: 20px;
  }
  .jumbotron .border-info {
    border-color: #17a2b8 !important;
  }
  .jumbotron .border-success {
    border-color: #28a745 !important;
  }
  .jumbotron .border-danger {
    border-color: #dc3545 !important;
  }
  .jumbotron .border-warning {
    border-color: #ffc107 !important;
  }
  .jumbotron .mt-3,
  .jumbotron .my-3 {
    margin-top: 20px !important;
  }
  .jumbotron .mt-2,
  .jumbotron .my-2 {
    margin-top: -10px !important;
  }
  @media screen and (min-width: 769px) and (max-width: 992px) {
    .my-card {
      position: absolute;
      left: calc(50% - 21px) !important;
      top: -20px;
      border-radius: 50%;
    }
    .col-counter {
      padding-top: 30px;
    }
    .service-about2::before {
      position: absolute;
      content: '';
      width: 35%;
      top: calc(50% + 2px);
      margin-left: 20px;
      border: 1px solid #03C4EB;
    }
  }
  @media screen and (max-width: 768px) {
    .my-card {
      left: calc(50% - 23px) !important;
    }
    .col-counter {
      padding-top: 30px;
    }
    .service-about2{
      &::after, &::before {
        display: none;
      } 
    }
  }