.img-header {
    margin: 0 auto;
}
.header-phone {
    .swiper-container {
        .swiper-button-next {
            opacity: 0;
        }
        .swiper-button-prev {
            opacity: 0;        
        }
        &:hover .swiper-button-next{
            opacity: 1;
        }
        &:hover .swiper-button-prev{
            opacity: 1;
        }
    }
}
.filter {
    padding: 10px 0;
    .manu {
        overflow: hidden;
        a {
            float: left;
            border: 1px solid #eee;
            height: 50px;
            line-height: 50px;
            position: relative;
            text-decoration: none;
            img {
                max-height: 100%;
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                margin: auto;
            }
        }
    }
    .manu7 a {
        width: calc(100% / 7);
    }
}