.feedback {
  padding-bottom: 30px;
}
.feedback-review{
    text-align: center;
    color: #03C4EB;
    position: relative;
}
.title-review {
    text-align: center;
    padding-bottom: 20px;
}
.img-review {
    overflow: hidden;
    border-radius: 50%;
    width: 200px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
.reviews {
    margin-top: 15px;
    color: #000;
    position: relative;
    font-size: 18px;
    text-align: center;
  }
  .color-reviews {
    color: #03C4EB;
  }
  .swiper-progressbar {
     margin-top: 30px;
     display: block;
    border-radius: 4px;
    transition: border .2s ease-in-out;
    overflow: hidden;
    border: 4px solid #e7e7e7;
  }
  .swiper-progressbar img {
    margin-top: 15px;
    padding: 5px;
    transition-duration: 0.5s;
}
.swiper-progressbar img:hover {
  transform: scale(1.2);
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -o-transform: scale(1.2);
  -ms-transform: scale(1.2);
  cursor: pointer;
}