
.fortest {
  padding-top:50px;
}

.navbar-default {
  width: 100%;
  background: #03C4EB;
  border: none;
  border-radius: none;
  margin: 0;
}
.nav-item {
  padding: 15px 10px;
}
.nav-item .nav-link {
  margin: 0;
  padding: 0;
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  position: relative;
}
.nav-item .sub-menu {
  position: absolute;
  text-align: center;
  background: #fff;
  margin-top: 15px;
  transition: 0.3s ease-in-out;
  opacity: 0;
  z-index: 10;
  list-style: none;
  visibility: hidden;
}
.nav-item .sub-menu .fath-one a {
  color: #333131;
  text-decoration: none;
}
.nav-item .sub-menu .fath-one .sub-menu-one .son-one {
  padding: 15px 35px;
  border-bottom: 1px solid #e4d0d0;
}
.nav-item .sub-menu .fath-one .sub-menu-one .son-one .a-one {
  color: #333131;
}
.nav-item .sub-menu .fath-one .sub-menu-one .son-one .a-one:hover {
  color: #d65679;
}
.nav-item .one-li-sub {
  padding: 0px 35px;
  width: 200px;
}
.nav-item .one-li-sub .fath-one {
  padding: 15px 10px;
  float: none !important;
  border-bottom: 1px solid #b9b6b6;
  position: relative;
}
.nav-item .one-li-sub .fath-one a:hover {
  color: #d65679;
}
.nav-item .one-li-sub .fath-one .sub-menu-one {
  position: absolute;
  background: #ffffff;
  transform: translateX(0);
  width: 100%;
  transition: 0.2s;
  top: 0;
  visibility: hidden;
}
.nav-item .one-li-sub .fath-one:hover .sub-menu-one {
  transform: translateX(100%);
  visibility: initial;
  border-left: 1px solid #b9b6b6;
  border-right: 1px solid #b9b6b6;
}
.nav-item:hover .sub-menu {
  transform: translateY(0px);
  opacity: 1;
  visibility: visible;
}
.navbar-right {
  padding-right: 30px;
}
.navbar-default .navbar-nav>li>a {
  color: #fff;
}
.navbar-default .navbar-brand {
  color: #fff;
}
.login-showform {
  position: relative;
  .closeLogin {
    position: absolute;
    left:calc(95% + 8px);
    top: 0;
    font-size: 30px;
    background: #03C4EB;
    color: #fff;
    padding: 5px 10px;
  }
  a {
    text-decoration: none;
  }
}
.btn-formLogin {
  background: #03C4EB;
  color: #fff;
  padding: 5px 10px;
  border: none;
  font-size: 16px;
  margin-top: 8px;
  outline: none;
}
.tabs-form {
  padding-top: 60px;
}
.tabs-login {
  background: #fff;
  color: #03c4eb;
  padding: 10px;
  position: absolute;
  text-align: center;
  width: 40%;
  right: 50%;
  outline: none;
  list-style: none;
  border: 2px solid #b0b5b8;
  &--selected {
    background: #03c4eb;
    color: #fff;
    border-radius: 5px 5px 0 0;
  }
}
.tabs-signup {
  float: left;
  position: absolute;
  left: 50%;
  background: #fff;
  color: #03c4eb;
  padding: 10px;
  text-align: center;
  width: 40%;
  outline: none;
  border: 2px solid #b0b5b8;
  border-left: none;
  
}
.tabs-login:hover {
  background: #03c4eb;
  color: #fff;
}
.tabs-signup:hover {
  background: #03c4eb;
  color: #fff;
}
.tabpanel-form1 {
  padding-top: 50px;
}
.tabpanel-form2 {
  padding-top: 0;
}
.img-user {
  width: 70%;
  margin: 0 auto;
}
.input100 {
  outline: none;
  color: #000;
  font-size: 18px;
  height: 40px;
  padding: 0 20px;
}
.forgot-pass {
  padding-top: 20px;
}
.forgot-pass a{
  font-size: 18px;
  color: #000;
}
.forgot-pass a:hover{
  color: #03c4eb;
}
.btn-Login {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding-top: 20px;
}

.login100-form-btn {
  background-color: #03c4eb;
  font-size: 18px;
  color: #fff;
  line-height: 1.2;
  outline: none;
  width: 48%;
  border: none;
  height: 40px;
}
.login100-form-btn:hover {
  background-color: #000;
}
.center-login {
  padding-top: 50px;
}
.sb-menu {
  border: none;
  outline: none;
  background: #000;
  color: #fff;
  padding: 6px 6px 7px 6px;
}
.form-control {
  border-radius: 0px;
}
.navbar {
  border-radius: 0px;
}

.modal2 {
  display: none;
}

.modal2.modal--is-open2 {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(176, 180, 183, 0.8);
}
.modal-message2 {
  background: #f8f8f8;
  box-shadow: 0 0 15px #e4e4ec;
  position: fixed;
  width: 60%;
  height: 80%;
  left: 20%;
  right: 20%;
  top: 10%;
  bottom: 10%;
} 
.btn-closeLogin {
  padding: 7px 12px;
  position: absolute;
  top:-5px;
  outline: none;
  border: none;
  border-radius: 50%;
  background: #fff;
  color: #000;
  right: 0;
  margin-top: 5px;
}
.pad-login {
  padding-top: 40px;
}
.img-users-login {
  margin: 0 auto;
  width: 70%;
}
.input-group {
	position: absolute;
	width: 280px;
	transition: .5s;
}
.input-field {
	width: 100%;
	padding: 10px 0;
	margin: 5px 0;
	border-top: 0;
	border-left: 0;
	border-right: 0;
	border-bottom: 1px solid #999;
	outline: none;
	background: transparent;
}
.submit-btn {
	width: 85%;
	padding: 10px 30px;
	cursor: pointer;
	display: block;
	margin: auto;
	background: linear-gradient(to right, #ff105f, #ffad06);
	border: 0;
	outline: none;
	border-radius: 30px;
}
.check-box {
	margin: 30px 10px 30px 0;
}
.check-box span {
	color: #777;
	font-size: 12px;
	bottom: 68px;
	position: absolute;
}
input[type=checkbox], input[type=radio] {
  margin: 15px 0;
}
@media screen and (min-width: 768px) and (max-width: 992px) { 
  .modal-message2 {
    width: 70%;
    left: 15%;
    right: 15%;
  } 
}
@media screen and (min-width: 768px) and (max-width: 830px) {
  .btn-formLogin{
    font-size: 12px;
  }
  .nav-item .sub-menu .fath-one a {
    font-size: 12px;
  }
  .navbar-default .navbar-nav > li > a {
    font-size: 12px;
  }
  .navbar-default .navbar-brand {
    font-size: 12px;
  }
  .navbar-form {
    padding: 0;
    margin: 0;
    border: none;
  }
  .sb-menu {
    margin-top: 9px;
  }
}
@media screen and (max-width: 767px) {
  .navbar-default .navbar-collapse, .navbar-default .navbar-form {
    border: none;
    border-color: #03C4EB;
    margin: 0;
    padding: 0;
  }
  .form-control{
    width: 50%;
    float: left;
  }
  .navbar-form .form-group {
    margin-bottom: 8px !important;
  }
  .navbar-collapse {
    display: none;
  }
  .nav-item .nav-link {
    margin-left: 15px;
  }
  .btn-formLogin { 
    margin-left: 18px;
    margin-top: -20px !important;
  }
  .nav-item {
    padding: 0 !important;
    padding-left: 15px !important;
  }
  .nav-item:hover .sub-menu {
    transform: translateY(0);
    transform: translateX(100px);
    position: absolute;
    top: -75%;
    opacity: 1;
    visibility: visible;
  }
  .nav-item .sub-menu {
    transition: 0s ease-in-out;
  }
  .login-title {
    display: none;
  }
  .img-users-login {
    display: none;
  }
}