.countdown-promotions {
	margin-top: 30px;
	margin-bottom: 30px;
}
.countdown-date {
	background-image: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), url(../img/bg4.jpg);
	background-repeat: no-repeat;
    width: 100%;
    height: 500px;
}
.countdown-timer22 {
	text-align: center;
	margin-bottom: 50px;
	padding-top: 130px;
    h2 {
        font-weight: bold;
        position: relative;
        &::after {
            position: absolute;
            content: '';
			width: 16%;
			top: 100%;
			left: 41%;
			border: 2px solid #e7ab3c;
        }
	}
	p {
		color: #000;
	}
}

.countdown-timer22 .cd-item {
	display: inline-block;
	background: #ffffff;
	margin-right: 25px;
	padding: 10px 15px;
	border-radius: 5px;
}

.countdown-timer22 .cd-item:last-child {
	margin-right: 0;
}

.countdown-timer22 .cd-item span {
	color: #e7ab3c;
	font-weight: 700;
	line-height: 40px;
	font-size: 36px;
}

.countdown-timer22 .cd-item p {
	font-size: 16px;
	color: #b2b2b2;
	margin-bottom: 0;
	text-transform: uppercase;
	letter-spacing: 1.5px;
}

.btn-goto {
	margin: 20px 0;
	background: #d45a5a;
	color: #fff;
	border: none;
	height: 50px;
	position: relative;
	font-size: 23px;
	padding: 0 15px;
	cursor: pointer;
	transition: 800ms ease all;
	outline: none;
	&:hover {
		background: #fff;
		color: #d45a5a;
	}
	&::before, &::after {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		height: 2px;
		width: 0;
		background: #d45a5a;
		transition: 400ms ease all;
	}
  }
  .btn-goto:after {
	right: inherit;
	top: inherit;
	left: 0;
	bottom: 0;
  }
  .btn-goto:hover:before,
  .btn-goto:hover:after {
	width: 100%;
	transition: 800ms ease all;
  }
  .goto2 {
	  margin-left: 20px;
  }
  .btn-jean {
	  padding-top: 260px;
	  text-align: center;
  }