@media screen and (min-width: 768px) and (max-width: 991px) {
    .filter .manu7 a {
        width: calc(100% / 5);
    }
    .top10-phone {
        width: 60%;
    }
    .allproduct_pic {
        height: 220px;
    }
}
@media screen and (max-width: 767px) {
    .filter .manu7 a {
        width: calc(100% / 4);
    }
    .top10-phone {
        width: 60%;
    }
    .img-product-size {
        width: 80%;
        margin: 0 auto;
    }
}
@media screen and (max-width: 535px) {
    .filter .manu7 a {
        width: calc(100% / 4);
    }
    .top10-phone {
        width: 80%;
    }
}